import styled from 'styled-components/macro';

import { categories as categoryList } from '../../categories';
import { kinds } from '../../kinds';
import Icon from '../Icon/Icon';

const generateUniqueCategories = categories =>
  categories
    .map(category => categoryList[category])
    // return only unique categories by display name
    .filter(
      (category, index, array) =>
        index === array.findIndex(c => c.display === category.display)
    );

const FunctionCard = ({ name, description, categories, kind, docfile, versions }) => (
  <Link href={`/function/${name}`}>
    <Wrapper className={name === 'aggregate' && 'active'}>
      <ImageWrapper>No Image</ImageWrapper>
      <Name>{name}</Name>
      <Info>
        {kinds[kind]} | Version: {Object.keys(versions)[0]} | MLRun 0.5.4+
      </Info>
      <Description>{description}</Description>
      <Categories>
        {generateUniqueCategories(categories).map(
          ({ display = '', color = '', contrast = false }) => (
            <Category
              key={display}
              style={{
                '--bg-color': color,
                '--color': contrast ? 'var(--color-black)' : 'inherit',
              }}
            >
              {display}
            </Category>
          )
        )}
      </Categories>
      <Framework>Framework: pandas</Framework>
      {name === 'aggregate' && <RocketIcon size="20" title="Rocket icon" />}
    </Wrapper>
  </Link>
);

const Link = styled.a`
  text-decoration: none;
  color: inherit;
`;

const Wrapper = styled.article`
  position: relative;
  background-color: hsla(218 25% 29%);
  height: 100%;
  padding: 32px;
  font-size: ${12 / 16}rem;
  color: var(--color-white);
  font-weight: 500;
  border: 1px solid transparent;
  border-bottom-width: 5px;
  border-radius: 4px;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  justify-content: flex-start;

  &:hover {
    border-color: hsl(349 56% 55%);
  }
`;

const ImageWrapper = styled.div`
  position: relative;
  width: 237px;
  height: 98px;
  border-radius: 4px;
  background-color: hsla(0 0% 100% / 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: ${18 / 16}rem;
`;

const Name = styled.h3`
  margin-top: 24px;
  font-size: ${16 / 16}rem;
  font-weight: 600;
`;

const Info = styled.span`
  color: hsla(0 0% 100% / 0.5);
`;

const Description = styled.p`
  margin-top: 16px;
  margin-bottom: 24px;
`;

const Categories = styled.ul`
  margin-top: auto;
  display: flex;
  flex-flow: row wrap;
  gap: 8px;
`;

const Category = styled.li`
  border-radius: 2px;
  background-color: var(--bg-color);
  color: var(--color);
  padding: 2px 8px;
`;

const Framework = styled.div`
  width: fit-content;
  margin-top: 8px;
  color: hsl(0 0% 60%);
  border: 1px solid hsl(0 0% 60%);
  padding: 2px 8px;
`;

const RocketIcon = styled(Icon).attrs(() => ({ id: 'rocket' }))`
  position: absolute;
  right: 16px;
  bottom: 16px;
`;

export default FunctionCard;
