import styled from 'styled-components/macro';

import { ReactComponent as Checked } from '../../images/checked.svg';
import { ReactComponent as ChevronDown } from '../../images/chevron-down.svg';
import { ReactComponent as Filter } from '../../images/fa-solid_sliders-h.svg';
import { ReactComponent as Search } from '../../images/search.svg';
import { ReactComponent as Rocket } from '../../images/rocket.svg';
import { ReactComponent as Sort } from '../../images/fa-solid_sort-amount-down.svg';
import { ReactComponent as Unchecked } from '../../images/unchecked.svg';

const icons = {
  checked: Checked,
  'chevron-down': ChevronDown,
  filter: Filter,
  rocket: Rocket,
  search: Search,
  sort: Sort,
  unchecked: Unchecked,
};

const Icon = ({ id, size = 24, ...restProps }) => {
  const Component = icons[id];
  return (
    <Wrapper style={{ '--size': `${size}px` }} {...restProps}>
      <Component color="currentColor" width={size} height={size} />
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: inline-block;
  width: var(--size);
  height: var(--size);
`;

export default Icon;
