import styled from 'styled-components/macro';

import Checkbox from '../Checkbox/Checkbox';
import Icon from '../Icon/Icon';
import Select from '../Select/Select';
import { kinds } from '../../kinds';
import { uniqueCategories } from '../../categories';

const toggleListItem = (list, item, exists) => {
  return !exists
    ? list.filter(listItem => listItem !== item)
    : !list.includes(item)
    ? [...list, item]
    : list;
};

const SideBar = ({
  selectedKinds,
  onKindToggle,
  selectedCategories,
  onCategoryToggle,
  sortBy,
  onSortBy,
}) => {
  const generateCategoryToggleHandler = key => event => {
    const newState = toggleListItem(selectedCategories, key, event.target.checked);
    onCategoryToggle(newState);
  };
  const generateKindToggleHandler = key => event => {
    const newState = toggleListItem(selectedKinds, key, event.target.checked);
    onKindToggle(newState);
  };
  return (
    <Wrapper>
      <Sort>
        <SortIcon id="sort" size="24" />
        <Heading>Sort</Heading>
        <SelectWrapper>
          <Select value={sortBy} onChange={onSortBy} label="Sort By">
            <option value="name-asc">By Name (A to Z)</option>
            <option value="name-desc">By Name (Z to A)</option>
            <option value="created">Last Created</option>
            <option value="modified">Last Modified</option>
          </Select>
        </SelectWrapper>
      </Sort>
      <Filters>
        <Icon id="filter" sie="24" />
        <Heading>Filters</Heading>
      </Filters>
      <Categories>
        <Heading>Categories</Heading>
        <CheckboxList>
          {Object.entries(uniqueCategories).map(([key, { display, color }]) => (
            <CheckboxItem key={key}>
              <Label style={{ color }}>
                <Checkbox
                  value={selectedCategories.includes(key)}
                  onChange={generateCategoryToggleHandler(key)}
                />
                {display}
              </Label>
            </CheckboxItem>
          ))}
        </CheckboxList>
      </Categories>
      <Kinds>
        <Heading>Kind</Heading>
        <CheckboxList>
          {Object.entries(kinds).map(([key, value]) => (
            <CheckboxItem key={key}>
              <Label>
                <Checkbox
                  value={selectedKinds.includes(key)}
                  onChange={generateKindToggleHandler(key)}
                />
                {value}
              </Label>
            </CheckboxItem>
          ))}
        </CheckboxList>
      </Kinds>
    </Wrapper>
  );
};

const Wrapper = styled.aside`
  flex: none;
  flex-basis: 300px;
  color: var(--color-white);
`;

const Section = styled.section`
  padding: 14px 0;

  h3 {
    font-weight: 600;
  }

  &:not(:first-of-type) {
    border-top: 1px solid hsla(0 0% 100% / 0.2);
  }
`;

const Heading = styled.h3`
  font-weight: 600;
`;

const Sort = styled(Section)`
  display: flex;
  align-items: baseline;
  gap: 8px;
  margin-bottom: 18px;
`;

const SortIcon = styled(Icon)`
  align-self: center;
`;

const SelectWrapper = styled.div`
  flex: auto;
`;

const Filters = styled(Section)`
  display: flex;
  gap: 8px;
  font-weight: 600;
`;

const Categories = styled(Section)``;

const Kinds = styled(Section)``;

const CheckboxList = styled.ul`
  margin-top: 14px;
  display: flex;
  flex-flow: column nowrap;
  gap: 14px;
`;

const CheckboxItem = styled.li``;

const Label = styled.label`
  display: flex;
  flex: row nowrap;
  align-items: center;
  gap: 8px;
`;

export default SideBar;
