import styled from 'styled-components/macro';

import Icon from '../Icon/Icon';
import HiddenContent from '../HiddenContent/HiddenContent';

const Input = ({
  label,
  icon,
  iconSize = 24,
  width = 250,
  height = 42,
  fontSize = 16,
  ...restProps
}) => {
  return (
    <Wrapper>
      <HiddenContent>{label}</HiddenContent>
      {icon && (
        <IconWrapper style={{ '--size': `${iconSize}px` }}>
          <Icon id={icon} size={iconSize} />
        </IconWrapper>
      )}
      <TextInput
        {...restProps}
        style={{
          '--width': width === 'full' ? '100%' : `${width}px`,
          '--height': `${height / 16}rem`,
          '--font-size': `${fontSize / 16}rem`,
          '--padding-left': icon ? 'var(--height)' : '8px',
        }}
      />
    </Wrapper>
  );
};

const Wrapper = styled.label`
  display: block;
  position: relative;
  color: hsla(0 0% 100% / 0.8);
`;

const IconWrapper = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  left: 9px;
  height: var(--size);
`;

const TextInput = styled.input`
  width: var(--width);
  height: var(--height);
  font-size: var(--font-size);
  background: none;
  border: 1px solid hsla(0 0% 100% / 0.2);
  padding-right: 8px;
  padding-left: var(--padding-left);
  color: inherit;
  font-weight: 600;
  outline-offset: 2px;
  &::placeholder {
    font-weight: 600;
    color: hsla(0 0% 100% / 0.2);
  }
`;

export default Input;
