import styled from 'styled-components/macro';
import Icon from '../Icon/Icon';

const Checkbox = ({ value = false, ...restProps }) => {
  const iconId = value ? 'checked' : 'unchecked';
  return (
    <IconWrapper>
      <NativeCheckbox checked={value} {...restProps} />
      <Icon id={iconId} size="20" />
    </IconWrapper>
  );
};

const IconWrapper = styled.span`
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
`;

const NativeCheckbox = styled.input.attrs(() => ({
  type: 'checkbox',
}))`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
`;

export default Checkbox;
