import { useEffect, useState } from 'react';
import styled from 'styled-components/macro';

import MaxWidthWrapper from '../MaxWidthWrapper/MaxWidthWrapper';
import SideBar from '../SideBar/SideBar';
import FunctionList from '../FunctionList/FunctionList';
import Input from '../Input/Input';
import { categories as categoriesMap } from '../../categories';

const CATALOG_URL =
  'https://raw.githubusercontent.com/mlrun/functions/master/catalog.json';

const Main = () => {
  const [name, setName] = useState('');
  const [sortBy, setSortBy] = useState('name-asc');
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedKinds, setSelectedKinds] = useState([]);
  const [functionList, setFunctionList] = useState([]);
  const [catalog, setCatalog] = useState({});

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  const handleNameChange = event => {
    setName(event.target.value);
  };
  const handleSortByChange = event => {
    setSortBy(event.target.value);
  };
  const handleCategoryToggle = newSelectedCategories => {
    setSelectedCategories(newSelectedCategories);
  };
  const handleKindToggle = newSelectedKinds => {
    setSelectedKinds(newSelectedKinds);
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(CATALOG_URL);
        const json = await response.json();
        setCatalog(json);
        setLoading(false);
      } catch (error) {
        setLoading(false);
        setError(true);
      }
    };
    setLoading(true);
    setError(false);
    fetchData();
  }, []);

  useEffect(() => {
    const filteredCatalog = Object.entries(catalog)
      .filter(
        ([key, { kind, categories }]) =>
          (!name || key.includes(name)) &&
          (selectedKinds.length === 0 || selectedKinds.includes(kind)) &&
          (selectedCategories.length === 0 ||
            categories.some(category =>
              selectedCategories.includes(categoriesMap[category]?.key ?? category)
            ))
      )
      .sort(([key1], [key2]) =>
        sortBy === 'name-asc'
          ? key1.localeCompare(key2, 'en', { sensitivity: 'base' })
          : sortBy === 'name-desc'
          ? key2.localeCompare(key1, 'en', { sensitivity: 'base' })
          : 1
      );
    setFunctionList(filteredCatalog);
  }, [catalog, name, sortBy, selectedCategories, selectedKinds]);
  return (
    <Wrapper>
      {loading && <Message>Loading…</Message>}
      {!loading && error && (
        <Message>Something went wrong… Could not fetch function catalog 😞</Message>
      )}
      {!loading && !error && (
        <Content>
          <SideBar
            selectedCategories={selectedCategories}
            onCategoryToggle={handleCategoryToggle}
            selectedKinds={selectedKinds}
            onKindToggle={handleKindToggle}
            sortBy={sortBy}
            onSortBy={handleSortByChange}
          />
          <FunctionIndex>
            <SearchFunction>
              <Input
                label="Search function"
                icon="search"
                iconSize="30"
                width="full"
                placeholder="Search Function"
                onChange={handleNameChange}
                value={name}
              />
            </SearchFunction>
            <FunctionList functionList={functionList} />
          </FunctionIndex>
        </Content>
      )}
    </Wrapper>
  );
};

const Wrapper = styled.main`
  flex: auto;
  margin: 24px 0;
  color: var(--color-white);
`;

const Content = styled(MaxWidthWrapper)`
  display: flex;
  flex-flow: row nowrap;
  align-items: baseline;
  gap: 30px;
`;

const FunctionIndex = styled.div`
  flex: auto;
`;

const SearchFunction = styled.div`
  margin-bottom: 33px;
`;

const Message = styled.h3`
  text-align: center;
`;

export default Main;
