import styled from 'styled-components/macro';

import FunctionCard from '../FunctionCard/FucntionCard';

const FucntionList = ({ functionList }) => {
  return (
    <Wrapper>
      {functionList.length === 0 && <p>No results found. Try to broaden your search.</p>}
      {functionList.map(([key, item]) => (
        <FucntionWrapper key={key}>
          {<FunctionCard {...item} name={key} />}
        </FucntionWrapper>
      ))}
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 30px;
`;

const FucntionWrapper = styled.div`
  width: 300px;
  min-height: 375px;
`;

export default FucntionList;
