import styled from 'styled-components/macro';

const HiddenContent = ({ children }) => <Wrapper>{children}</Wrapper>;

const Wrapper = styled.div`
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
`;

export default HiddenContent;
