import React from 'react';
import styled from 'styled-components/macro';

import HiddenContent from '../HiddenContent/HiddenContent';
import Icon from '../Icon/Icon';

const Select = ({ label, value, onChange, children }) => {
  const childArray = React.Children.toArray(children);
  const selectedChild = childArray.find(child => child.props.value === value);
  const displayedValue = selectedChild.props.children;
  return (
    <Wrapper>
      <HiddenContent>{label}</HiddenContent>
      <NativeSelect value={value} onChange={onChange}>
        {children}
      </NativeSelect>
      <MockedSelect>
        {displayedValue}
        <IconWrapper>
          <Icon id="chevron-down" size="20" />
        </IconWrapper>
      </MockedSelect>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  position: relative;
  width: 100%;
`;

const NativeSelect = styled.select`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
`;

const MockedSelect = styled.div`
  color: var(--color-white);
  background: none;
  font-size: ${14 / 16}rem;
  font-weight: 400;
  padding: 12px 16px;
  padding-right: 52px;
  border-radius: 8px;

  ${NativeSelect}:focus + & {
    outline: 1px dotted #212121;
    outline: 5px auto -webkit-focus-ring-color;
  }

  ${NativeSelect}:hover + & {
    background-color: hsl(0 0% 50% / 0.2);
  }
`;

const IconWrapper = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10px;
  margin: auto;
  width: 20px;
  height: 20px;
  pointer-events: none;
`;

export default Select;
